import React from "react";
import classes from "./Movie.module.css";
import { NavLink } from "react-router-dom";

const Movie = ({ data, id, history }) => {
	let movie = data.find((m) => m.movieId === +id);
	let movieData;

	if (movie) {
		movieData = (
			<div>
				<h1>{movie.movieName}</h1>
				<NavLink to="/">Go home</NavLink>
			</div>
		);
	} else {
		movieData = <h1>Sorry. Movie not found</h1>;
	}
	return <div className={classes.movie}>{movieData}</div>;
};

export default Movie;
