import React, { useState } from "react";
import classes from "./FavoritesButton.module.css";

const FavoritesButton = ({ style, form }) => {
	const [isFavorite, setIsFavorite] = useState(false);
	const cls = [classes.favorites];

	if (form === "white") cls.push(classes.favoritesWhite);

	return (
		<span
			className={cls.join(" ")}
			style={{ ...style }}
			onClick={() => setIsFavorite(!isFavorite)}
		>
			{isFavorite ? (
				"in "
			) : (
				<span
					style={{ fontSize: form === "white" ? 25 : null }}
					className={classes.favoritesPlus}
				>
					+
				</span>
			)}
			Favorites
		</span>
	);
};

export default FavoritesButton;
