import React from "react";
import classes from "./ShortMovieInfo.module.css";

const ShortMovieInfo = ({ name, genres }) => {
	let genresData = Array.isArray(genres)
		? genres.map((genre, id) => {
				return (
					<span key={id} className={classes.genre}>
						{genre}
					</span>
				);
		  })
		: genres;

	return (
		<div>
			<div>
				<p className={classes.movieName}>{name}</p>
				<p className={classes.genres}>{genresData}</p>
			</div>
		</div>
	);
};

export default ShortMovieInfo;
