import React from "react";
import classes from "./Dropdown.module.css";

const Dropdown = ({ buttons, style, onClick }) => {
	return (
		<div className={classes.dropdown} style={{ ...style }}>
			{buttons.map((button, id) => {
				return (
					<p key={id} className={classes.button} onClick={onClick}>
						<i className={classes.icon + ` ${button.icon}`}></i>
						{button.name}
					</p>
				);
			})}
		</div>
	);
};

export default Dropdown;
