import React, { useState } from "react";
import classes from "./Card.module.css";
import Menu from "../../UI/Menu/Menu";
import MainButton from "../../UI/MainButton/MainButton";
import Rating from "../../UI/Rating/Rating";
import Dropdown from "../../UI/Dropdown/Dropdown";
import PlayButton from "./PlayButton/PlayButton";
import ShortMovieInfo from "./ShortMovieInfo/ShortMovieInfo";
import FavoritesButton from "../../UI/FavoritesButton/FavoritesButton";

const Card = ({
	movieName,
	movieGenres,
	movieRating,
	movieLink,
	movieDescription,
	movieImage,
	movieId,
}) => {
	const [isExtended, setIsExtended] = useState(false);
	const [isExtendedDelay, setIsExtendedDelay] = useState(false);
	const [isExpanded, setIsExpanded] = useState(false);

	setTimeout(() => {
		if (isExtended) setIsExtendedDelay(true);
	}, 550);

	let sliced = movieDescription.slice(0, 190);
	if (sliced.length < movieDescription.length) {
		sliced += "...";
	}

	const dropdownButtons = [
		{ icon: "fa fa-history", name: "Watch later" },
		{ icon: "fa fa-remove", name: "Don't show" },
	];

	return (
		<div className={classes.card}>
			<div className={classes.imageWrapper}>
				<img
					alt={movieName}
					className={classes.picture}
					style={{
						height: isExtended ? 410 : 300,
						borderRadius: isExtended ? 5 : null,
					}}
					src={movieImage}
				/>
				{isExtendedDelay ? (
					<div className={classes.infoOverlay}>
						<div className={classes.infoTopButtons}>
							<div>
								<span
									className={classes.infoClose}
									onClick={() => {
										setIsExtended(false);
										setIsExtendedDelay(false);
									}}
								>
									&#10006;
								</span>
							</div>

							<div style={{ position: "relative" }}>
								<FavoritesButton />
								<Menu
									onClick={() => setIsExpanded(!isExpanded)}
									style={{ position: "relative", top: 5 }}
								/>
								{isExpanded ? (
									<Dropdown
										buttons={dropdownButtons}
										onClick={() => setIsExpanded(!isExpanded)}
									/>
								) : null}
							</div>
						</div>

						<div className={classes.infoShortDescription}>
							<ShortMovieInfo name={movieName} genres={movieGenres} />
							<Rating score={movieRating} link={movieLink} />
						</div>

						<div>
							<p className={classes.infoDescription}>{sliced}</p>
						</div>

						<div style={{ textAlign: "left", padding: "0 20px" }}>
							<MainButton text="Watch now" form="blue" />
						</div>
					</div>
				) : (
					<div
						className={classes.overlay}
						style={{
							backgroundColor: isExtended ? "rgba(0, 0, 0, 0.5" : null,
							opacity: isExtended ? 1 : null,
						}}
					>
						<div
							className={classes.overlayContent}
							style={{ transform: isExtended ? "translateY(-400px)" : null }}
						>
							<div>
								<PlayButton id={movieId} />
							</div>
							<div>
								<MainButton
									text="View info"
									onClick={() => setIsExtended(true)}
									style={{ marginTop: 35 }}
								/>
							</div>
						</div>
					</div>
				)}
			</div>

			<div
				className={classes.shortDescription}
				style={{
					height: isExtended ? 0 : null,
					padding: isExtended ? "0px 5px" : null,
				}}
			>
				<ShortMovieInfo name={movieName} genres={movieGenres} />
				<Rating score={movieRating} link={movieLink} />
			</div>
		</div>
	);
};

export default Card;
